.profile__description {
  margin: 0;
  padding-top: 16px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  max-width: 500px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 619px) {
  .profile__description {
    padding-top: 14px;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }
}